<template>
    <el-row :gutter="20">
        <el-col :span="24" class="leftReset">
            <el-form label-position="right">
                <el-row type="flex" align="middle">
                    <el-col :span="4">
                        <div class="form-group-title">任务列表</div>
                    </el-col>
                </el-row>

                <el-row type="flex" align="middle">
                    <el-col :span="24">
                        <div class="filter-container filter-div">
                            <span class="filter-label">任务编号</span>
                            <el-input class="filter-input" v-model="filter.taskId" placeholder="任务编号"></el-input>
                            <span class="filter-label">任务状态</span>
                            <el-select class="filter-select" v-model="filter.taskStatus" placeholder="请选择">
                                <el-option v-for="item in taskStatusList" :key="item.value" :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>
                            <span class="filter-label">项目编号</span>
                            <el-input class="filter-input" v-model="filter.projectNo" placeholder="项目编号"></el-input>
                            <span class="filter-label">项目名称</span>
                            <el-input class="filter-input" v-model="filter.projectName" placeholder="项目名称"></el-input>
                            <span class="filter-label">项目状态</span>
                            <el-select class="filter-select" v-model="filter.projectStatus" placeholder="请选择">
                                <el-option v-for="item in projectStatusList" :key="item.value" :label="item.label"
                                           :value="item.value">
                                </el-option>
                            </el-select>

                            <el-button type="primary" icon="search" @click="handleFilter">查询</el-button>
                        </div>
                    </el-col>

                </el-row>
                <table class="table">
                    <thead class="thead">
                    <tr>
                        <th>任务编号</th>
                        <th>项目编号</th>
                        <th>项目名称</th>
                        <th>工作内容</th>
                        <th>样品数量</th>
                        <th>计划开始时间</th>
                        <th>计划完成时间</th>
                        <th>实际开始时间</th>
                        <th>实际完成时间</th>
                        <th>任务状态</th>
                        <th>操作员</th>
                        <th>操作</th>
                    </tr>
                    </thead>

                    <tbody class="tbody">
                    <tr v-for="(list, $index) in tableDataList">
                        <td>{{list.id}}</td>
                        <td>{{list.project.no}}</td>
                        <td>{{list.project.name}}</td>
                        <td>{{list.job.typeDesc}}</td>
                        <td>{{list.sampleAmount}}</td>
                        <td>{{list.planStartTime | formatDate}}</td>
                        <td>{{list.planCompleteTime | formatDate}}</td>
                        <td>{{list.actualStartTime | formatDate}}</td>
                        <td>{{list.actualCompleteTime | formatDate}}</td>
                        <td>{{list.statusDesc}}</td>
                        <td>{{list.operator==null?null:list.operator.nickname}}</td>
                        <td class="modifyTrData">
                            <el-button type="primary" @click='viewDetail(list)' size="small">查看</el-button>
                        </td>
                    </tr>
                    </tbody>
                </table>

                <el-row type="fix" align="middle" style="margin-top: 20px">
                    <el-col :span="16">
                        <el-pagination
                                @size-change="handleSizeChange"
                                @current-change="handleCurrentChange"
                                :current-page="filter.currentPage"
                                :page-sizes="[10, 20, 30, 40]"
                                :page-size="filter.pageSize"
                                layout="total, sizes, prev, pager, next, jumper"
                                :total="totalCount">
                        </el-pagination>
                    </el-col>
                </el-row>
            </el-form>
        </el-col>
    </el-row>

</template>

<script>
    import ElCol from "element-ui/packages/col/src/col";
    import Task from '@/api/task';

    export default {
        components: {
            ElCol
        },
        mounted() {
            var path = this.$route.path;
            this.filter.taskStatus = Number(path.substr(path.lastIndexOf("/") + 1));
            if (!this.filter.taskStatus) {
                this.filter.taskStatus = null;
            }
            this.getData();
        },
        data() {
            return {
                filter: {
                    taskId: null,
                    projectNo: null,
                    projectStatus: null,
                    taskStatus: null,
                    projectName: null,
                    page: 1,
                    length: 10
                },
                tableDataList: [],
                totalPage: 0,
                totalCount: 0,
                projectStatusList: this.GLOBAL.projectStatusList,
                taskStatusList: this.GLOBAL.taskStatusList,
            };
        },
        methods: {
            viewDetail(task){
                this.$store.commit("setProjectNo", task.project.no);
                this.$store.commit("setProjectId", task.project.id);
                this.$router.push({
                    name: 'projectDetail',
                    query: {title: 'task',projectId:task.project.id}
                });
            },
            getData(){
                Task.getList(this.filter).then((response) => {
                    if (response.code == 0) {
                        this.tableDataList = response.data.rows;
                        this.totalPage = response.totalPage;
                        this.totalCount = response.data.totalCount;
                    }
                })
            },
            handleFilter(){
                this.getData();
            },
            // 分页
            handleSizeChange(val) {
                this.filter.length = val;
                this.getData();
            },
            handleCurrentChange(val) {
                this.filter.page = val;
                this.getData();
            },
        }
    };
</script>

<style lang="scss" scoped>
    @import "~styles/variable";

    .el-form-item span {
        width: 180px;
        display: inline-block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .seachpre {
        width: 74px;
    }

    .filter-div {
        margin: 1% auto;
    }

    .table-button {
        width: 100px;
        height: 36px;
        margin-left: 20px;
        margin-right: 20px !important;
        border: 1px solid $color-theme;
        border-radius: 4px !important;
    }

    .filter-date {
        width: 10%;
        margin-right: 1%;
    }

    .filter-input {
        width: 10%;
        margin-right: 1%
    }

    .filter-select {
        width: 10%;
        margin-right: 1%
    }

    .filter-label {
        font-size: 10pt;
    }

    .buttonWrapper {
        font-size: 12px;
        float: right;
        .confirm,
        .cancle {
            width: 76px;
            padding: 7px 15px;
        }
        .confirm {
            color: $color-theme;
            border: 1px solid $color-theme;
        }
        .cancle {
            color: $color-red;
            border: 1px solid $color-red;
        }
    }

    .tableEditWrapper {
        float: right;
        .edit {
            width: 102px;
            padding: 8px 15px;
            font-size: 13px;
            color: $color-theme;
            border: 1px solid $color-theme;
        }
        .cancle {
            width: 102px;
            padding: 8px 15px;
            font-size: 13px;
            color: $color-red;
            border: 1px solid $color-red;
        }
    }

    // 分页
    .el-pagination {
        text-align: right;
        padding: 10px 0;
        float: right;
    }

    .el-pagination__total {
        float: left;
    }

    .leftReset {
        padding-right: 20px !important;
    }

    .rightTitle {
        height: 50px;
        padding: 18px 0 18px 10px;
        line-height: 50px;
        box-sizing: border-box;
        background-color: #eff2f7;
        .form-group-title {
            margin: 0;
        }
    }

    // 表格
    .table {
        color: #2f3445;
        font-size: 13px;
        border: 1px solid #e0e6ed;
        .thead {
            background-color: #e5e9f2;
            th {
                width: 130px;
                height: 44px;
                text-align: center;
                border-right: 1px solid #e0e6ed;
            }
        }
        .tbody {
            tr {
                height: 46px;
                width: 130px;
                text-align: center;
                border-top: 1px solid #e0e6ed;
                td {
                    border-right: 1px solid #e0e6ed;
                }
                .modifyTrData {
                    color: #5c7dfe;
                }
            }
        }
    }
</style>


